
import Checklist from 'vux/src/components/checklist/index.vue'
import Group from 'vux/src/components/group/index.vue'

import VForm from "./components/form";
export default {
name: "Order",
components: {
Checklist,
Group,
VForm
},
data() {
return {
defaultList: ["于预定日携带身份证到现场指定地点领取"],
checkList: ["于预定日携带身份证到现场指定地点领取", "同城快递送票上门"],
type: 1
};
},
watch: {
defaultList(val, val2) {
var item = val[0];
if (item === "于预定日携带身份证到现场指定地点领取") {
this.type = 1;
} else if (item === "同城快递送票上门") {
this.type = 2;
} else {
this.defaultList = [val2[0]];
}
}
},
methods: {}
};



